//       <User avatar="https://s3.amazonaws.com/uifaces/faces/twitter/vladarbatov/128.jpg" username="Prajakta"/>
//       <div>
//         <TimeAgo>4d</TimeAgo>
//       </div>
//     </Header>
//   </CardContainer>
// );

export default ({data}) => {
  let fluid=data.images.edges[1].node.image.childImageSharp.fluid;
  return (<CardContainer>
    <Header>
      <User avatar="https://s3.amazonaws.com/uifaces/faces/twitter/vladarbatov/128.jpg" username="Prajakta"/>
      <div>
        <TimeAgo>4d</TimeAgo>
      </div>      
    </Header>
    <Img fluid={fluid}></Img>
  </CardContainer>);
}

export const pageQuery = graphql`
  query {
    images: allMergedImagesJson {
      edges {
        node {
          title
          author {
            name
          }
          image {
            childImageSharp {
              fluid(maxWidth: 300, traceSVG: { blackOnWhite: false }) {
                ...GatsbyImageSharpFluid_tracedSVG
              }
              original {
                src
              }
            }
          }
        }
      }
    }
  }
`;